      font-style: normal;
      font-weight: normal;
      font-size: 46px;
      line-height: 55px;
      margin: 10px 0;
      @media only screen and(max-width: 472px) {
        font-size: 30px;
        line-height: 38px; } }
    &__do {
      color: #c63;
      font-family: 'Gilroy Medium';
      font-style: normal;
      font-weight: normal;
      font-size: 25px;
      margin: 0 0 30px;
      @media only screen and(max-width: 472px) {
        font-size: 16px; } } }
  &__img {
    padding-bottom: 180px;
    @media only screen and(max-width: 472px) {
      margin-left: 35%;
      padding-bottom: 90px;
      width: 80px;
      height: 250px; } } }

.feedback {
  text-align: center;
  &__wrap {
    padding-top: 80px;
    position: relative; }
  &__cross {
    display: block;
    position: absolute;
    top: -70px;
    left: 50%;
    width: 405px;
    margin-left: -203px;
    z-index: -1;
    @media only screen and(max-width: 472px) {
      top: -25px;
      width: 300px;
      margin-left: -150px; }
    &__text {
      text-transform: uppercase;
      font-family: 'DOCKER One';
      font-weight: normal;
      font-style: normal;
      font-size: 120px;
      color: #ddd;
      @media only screen and(max-width: 472px) {
        font-size: 80px; } } }
  &__slider {
    // overflow: hidden
    padding: 50px 0 30px;
    @media only screen and(max-width: 773px) {
      padding: 0 0 15px; }
    @media only screen and(max-width: 538px) {
      padding: 0; }
    &__videowrapper {
      position: relative;
      padding: 20px;
      max-height: 415px;
      img {
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        height: 80px;
        width: 80px;
        opacity: 0;
        pointer-events: none;
        @media only screen and(max-width: 1115px) {
          height: 60px;
          width: 60px; }
        @media only screen and(max-width: 866px) {
          height: 50px;
          width: 50px; }
        @media only screen and(max-width: 688px) {
          width: 40px;
          height: 40px; }
        @media only screen and(max-width: 500px) {
          width: 30px;
          height: 30px; } } }
    &__wrap {
      width: 100%; } }
  &__controls {
    text-align: center;
    margin: 100px 0 0 0; }
  &__button {
    width: 460px;
    margin-bottom: 90px;
    @media only screen and(max-width: 475px) {
      display: block;
      width: 80%;
      height: 50px;
      font-size: 22px;
      margin: 20px auto 90px; }
    @media only screen and(max-width: 360px) {
      width: 90%; } } }

.video__feedback {
  height: 100%;
  width: 100%; }

//===========================Slider Presets====================================
.slick-dots {
  background: transparent;
  height: 30px;
  @media only screen and(max-width: 472px) {
    height: 0; }
  li {
    margin: 0 10px;
    @media only screen and(max-width: 472px) {
      margin: 0 2px; } } }

.slick-slide {
  outline: none;
  padding: 55px 0;
  opacity: .5;
  margin-bottom: 25px;
  z-index: 10;
  &>img {
    opacity: 0; }
  @media only screen and(max-width: 920px) {
    padding: 25px 0 20px; }
  @media only screen and(max-width: 674px) {
    padding: 20px 0 10px; }
  @media only screen and(max-width: 472px) {
    padding: 10px 0 0;
    margin-bottom: 0; } }

.slick-slide.slick-center {
  opacity: 1;
  transform: scale(1.25);
  max-height: 520px;
  z-index: 20;
  &>img {
    opacity: 1; } }

.slick-dots li button:before {
  font-size: 30px;
  @media only screen and(max-width: 920px) {
    font-size: 20px; }
  @media only screen and(max-width: 472px) {
    font-size: 14px; } }

.slick-dots li button:hover:before {
  color: #c60;
  opacity: .7; }

.slick-dots li.slick-active button:before {
  color: #c60;
  opacity: .9; }

.slick-dots li button:focus:before {
  color: #c60;
  opacity: 1; }
//==========================Slider Presets End==================================

