//.box-name {font-family: 'DOCKER One';}
@font-face {
  font-family: 'DOCKER One';
  src: url('../fonts/DOCKERONE.eot');
  src: url('../fonts/DOCKERONE.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/DOCKERONE.woff') format('woff');
  src: url('../fonts/DOCKERONE.ttf') format('truetype');
  font-weight: normal;
  font-style: normal; }

